<template>
  <div class="main">
    <!-- <div class="item" 
      v-for="(item, idx) in list"
      v-bind:style="{ borderColor: item.border, color: item.border }"
      :key="idx">
      <div class="text">{{item.desc}}</div>
      <router-link :to="{name: 'SelectCanvas', params: item}">
        <img class="pic" v-bind:src="item.imgs[0]"></img>
      </router-link>
    </div> -->
    <div class="item" 
      v-for="(item, idx) in list"
      :key="idx">
      <div class="index"
        v-bind:style="{ background: border }">{{idx+1}}
      </div>
      <div class="text">{{item.attributes.desc}}</div>
      <router-link :to="{name: 'SelectColor', params: {img: item.attributes.imgs.attributes.url, border: border, colors: colors } }">
        <img class="pic"
          v-bind:src="item.attributes.imgs.attributes.url"
          v-bind:style="{ borderColor: border }">
        </img>
      </router-link>
    </div>
  </div>
</template>

<script>
// import config from '../config'
import AV from 'leancloud-storage'

export default {
  data () {
    return {
      list: {},
      border: '#EECA7D',
      colors: [{
        'desc': '少女的第一盘眼影',
        'colors': ['#6F0051', '#971B40', '#FAB3AF', '#FCC584', '#F0D6E3', '#E2DEF0', '#EF766D', '#F2A6B3', '#FECFBB', '#FDE3B2', '#E97585', '#E95166']
      }, {
        'desc': '火烧云下有棵橄榄树',
        'colors': ['#4A0203', '#F4F6E0', '#F4DD8D', '#A7AA73', '#DA9075', '#CF5F46', '#A72F0D', '#F05639', '#A2102D', '#DB8C53', '#FFC672', '#EFD0A6']
      }, {
        'desc': '小河边上浮萍初现',
        'colors': ['#8BB297', '#8F8373', '#D8907A', '#C2D3A6', '#D3E28D', '#93D9F2', '#1E8B88', '#CAE37B', '#8D9C1F', '#6D7A66', '#CED8D9', '#B3C8BA']
      }, {
        'desc': '运动之后的第一口可乐',
        'colors': ['#C9F1EC', '#DAEDF4', '#B5E3F0', '#A4B5D1', '#3C5397', '#0057A1', '#B9BCF7', '#CF6D69', '#DC5454', '#C5B2B4', '#F0E5D1', '#EA893A']
      }, {
        'desc': '清凉薄荷',
        'colors': ['#DAEDF4', '#A5CECF', '#CFACAA', '#ED5E5A', '#9D3349', '#C2D996', '#0AA59D', '#248057', '#02585C', '#546C5E', '#568074', '#BBCDBD']
      }, {
        'desc': '黑白无常',
        'colors': ['#BE8FA1', '#F4F6F0', '#D4CEC8', '#D4C8D1', '#A86676', '#70495E', '#582836', '#3A4141', '#2D2C30', '#554349', '#827A7F', '#BAA7AD']
      }, {
        'desc': '雪后初霁',
        'colors': ['#D4E9FE', '#D6F4F5', '#A7C9CA', '#718BA6', '#5C658E', '#191D36', '#91CAE7', '#243676', '#6473B6', '#7A648B', '#585373', '#36304C']
      }]
    }
  },
  mounted () {
    document.title = '选择图案'
    new AV.Query('config').find().then((object) => {
      this.list = object
    }, (error) => {
      console.error(error)
      alert(error)
    })
  }
}
</script>

<style scoped>
.main {
  /*margin: 5vw;*/
}

.item {
  position: relative;
  margin: 2vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.index {
  /*background: #ff5800;*/
  color: #ffffff;
  width: 5vw;
  height: 5vw;
  line-height: 5vw;
  border-radius: 50%;
  font-size: 3vw;
  margin: 2vw;
  /*vertical-align: middle;*/
}
.pic {
  height: auto;
  width: 95vw;
  object-fit: cover;

  border: 1.5px solid;
  border-radius: 5vw;
  /*border-color: #ff5800;*/
}
</style>



